<template>
	<div style="display: flex ; align-itmes: center; justify-content: center;">
		<validation-observer ref="form">
			<form @submit.prevent="register" id="formData" enctype="multipart/form-data">
				<validation-provider name="  ">
					<img-inputer
						name="file"
						v-model="signUpMember.file"
						size="middle"
						placeholder="Drop file here or click"
						bottomText="Drop file here or click"
						exceedSizeText="  "
						:maxSize="10240"
						ref="profile"
						@onExceed="exceedHandler"
					/>
				</validation-provider>

				<!-- <v-gravatar :email="email" alt="gravatar" :size="50" /> -->

				<validation-provider name=" " rules="required|email" v-slot="{ errors }">
					<v-text-field
						name="id"
						v-model="signUpMember.id"
						label=""
						:error-messages="errors[0] ? errors[0] : []"
					></v-text-field>
				</validation-provider>
				<validation-provider name=" " rules="required|max:10" v-slot="{ errors }">
					<v-text-field
						name="nickname"
						v-model="signUpMember.nickname"
						:counter="10"
						label=""
						:error-messages="errors[0] ? errors[0] : []"
					></v-text-field>
				</validation-provider>
				<validation-provider
					name=" "
					vid="confirmation"
					rules="required|min:8|max:16|password"
					v-slot="{ errors }"
				>
					<v-text-field
						name="password"
						v-model="signUpMember.password"
						label=""
						:counter="16"
						:type="pwd1 ? 'text' : 'password'"
						:error-messages="errors[0] ? errors[0] : []"
						:append-icon="pwd1 ? 'mdi-eye' : 'mdi-eye-off'"
						@click:append="pwd1 = !pwd1"
					></v-text-field>
				</validation-provider>
				<validation-provider
					name="  "
					rules="required|confirmed:confirmation"
					v-slot="{ errors }"
				>
					<v-text-field
						v-model="passwordConfirm"
						label=" "
						:type="pwd2 ? 'text' : 'password'"
						:error-messages="errors[0] ? errors[0] : []"
						:append-icon="pwd2 ? 'mdi-eye' : 'mdi-eye-off'"
						@click:append="pwd2 = !pwd2"
					></v-text-field>
				</validation-provider>
				<v-text-field v-model="signUpMember.gitUrl" label="Git url()"></v-text-field>
				<v-text-field v-model="signUpMember.kakaoUrl" label="Kakao url()"></v-text-field>
				<v-text-field v-model="signUpMember.instagramUrl" label="Instagram url()"></v-text-field>

				<v-btn class="mr-4" type="submit"></v-btn>
				<v-btn @click="clear"></v-btn>
			</form>
			<div>
				<button v-on:click="idCheck"></button>
				<div v-if="duplicate">
					<div v-for="(message, idx) in duplicate" :key="idx">{{ message }}</div>
				</div>
			</div>
		</validation-observer>
	</div>
</template>

<script>
import http from "../http-common";
import router from "../router";
import {
	ValidationProvider,
	ValidationObserver,
	extend,
	validate
} from "vee-validate";

export default {
	name: "RegisterForm",
	components: {
		ValidationProvider,
		ValidationObserver
	},
	data: () => ({
		signUpMember: {
			file: "",
			id: "",
			nickname: "",
			password: "",
			email: "",
			gitUrl: "",
			kakaoUrl: "",
			instagramUrl: ""
		},
		pwd1: false,
		pwd2: false,
		passwordConfirm: "",
		duplicate: [],
		idcheck: false
	}),

	methods: {
		register() {
			let formData = new FormData(document.getElementById("formData"));
			formData.set("file", this.$refs.profile.file);

			if (this.onSubmit() && this.idcheck) {
				this.$store.dispatch("startLoading");
				console.log("REGISTER beforeaxios ", formData);
				http.post("/trc/signUp/", formData)
					.then(res => {
						console.log("REGISTER then ", res);
						this.$store.dispatch("endLoading");
						alert("  .");
						router.push("/");
					})
					.catch(err => {
						this.$store.dispatch("endLoading");
						console.log("REGISTER catch ", err);
					});
			} else {
				console.log("REGISTER ", " ");
			}
		},
		previewImage: function(event) {
			var input = event.target;
			if (input.files && input.files[0]) {
				var reader = new FileReader();
				reader.onload = e => {
					this.singUpMember.file = e.target.result;
				};
				reader.readAsDataURL(input.files[0]);
			}
		},
		onSubmit() {
			this.$refs.form.validate().then(success => {
				if (!success) {
					alert("  .");
					return false;
				}
			});
			return true;
		},
		clear() {
			this.$refs.form.reset();
		},
		valid() {
			this.$refs.form.validate();
		},
		idCheck() {
			if (this.signUpMember.id) {
				this.duplicate = [];
				console.log("DUPLICATE ", this.signUpMember.id);
				http.post("/api/check", {
					id: this.signUpMember.id
				})
					.then(res => {
						console.log("DUPLICATE then ", this.signUpMember.id);
						console.log("DUPLICATE then ", res);
						if (res.data) {
							this.duplicate.push(
								"  ."
							);
							this.idcheck = true;
						} else {
							this.duplicate.push(" .");
						}
					})
					.catch(err => {
						console.log("DUPLICATE catch ", err);
					});
			} else {
				this.duplicate.push("  .");
			}
		},
		exceedHandler(file) {
			console.log(this.$refs.profile);
			console.warn("onExceed -> file", file);
			// this.$refs.profile.reset();
		}
	},
	mounted() {
		// this.localize("ko", this.dictionary)
	},
	computed: {
		loading: function() {
			return this.$store.state.loading;
		}
	}
};
</script>

